<template>
  <v-flex xs12>
    <v-layout xs12 row>
      <v-checkbox
        v-if="toPredict.length > 0"
        label="Main"
        v-model="isActive.toPredict"
      ></v-checkbox>
      <v-checkbox
        v-if="prediction.length > 0"
        label="Prediction"
        v-model="isActive.prediction"
      ></v-checkbox>
      <v-checkbox
        v-for="(timeSerie, i) in timeSeries"
        :key="i"
        :label="'timeSerie-' + i"
        v-model="isActive[i]"
      ></v-checkbox>
    </v-layout>
    <svg :width="width" :height="height" class="grey darken-3"
    @mousewheel.prevent="zoom"
    @mousedown.exact="panEnabled = true"
    @mouseup="panEnabled = false"
    @mousemove="pan">
      <g :transform="`translate(${marginLeft}, ${marginTop})`">
        <defs>
          <clipPath id="clip-rect">
            <rect :width="chartWidth" :height="height" :y="-this.marginTop" />
          </clipPath>
        </defs>
        <g clip-path="url(#clip-rect)">
          <!-- linea a predecir (blue) -->
          <c-path
            v-if="isActive.toPredict"
            :transform="`translate(${-offsetX} 0)`"
            :rangeX="[zoomMin, zoomMax]"
            :rangeY="[globalMin, globalMax]"
            :dataset="toPredict"
            strokeWidth="4"
            column="y"
            :height="chartHeight"
            :width="chartWidth"></c-path>
          <!-- linea de prediccion (yellow) -->
          <c-path
            v-if="total.length > 0 && isActive.prediction"
            :transform="`translate(${$toXPixel(toPredict.length - 1 + zoomMin, zoomMin, zoomMax, chartWidth) - offsetX}, 0)`"
            color="yellow"
            strokeWidth="4"
            :rangeX="[zoomMin, zoomMax]"
            :rangeY="[globalMin, globalMax]"
            :dataset="prediction"
            column="y"
            :height="chartHeight"
            :width="chartWidth"></c-path>
          <!-- timeSeries -->
          <c-path
            v-for="(timeSerie, i) in timeSeries"
            :key="i"
            v-if="isActive[i]"
            :transform="`translate(${-offsetX} 0)`"
            color="#ffbf8bb8"
            :rangeX="[zoomMin, zoomMax]"
            :rangeY="[globalMin, globalMax]"
            :dataset="timeSerie"
            column="y"
            :height="chartHeight"
            :width="chartWidth"></c-path>
            <!-- lineas de debug -->
          <g v-for="(data, i) in debug"
            :key="i"
            v-if="!debug.isEmpty">
          <c-path
            :transform="`translate(${$toXPixel(toPredict.length - 1 + zoomMin, zoomMin, zoomMax, chartWidth) - offsetX}, 0)`"
            color="orange"
            :rangeX="[zoomMin, zoomMax]"
            :rangeY="[globalMin, globalMax]"
            :dataset="data"
            column="y"
            :height="chartHeight"
            :width="chartWidth"></c-path>
            <text fill="white"
            :transform="`translate(${$toXPixel((toPredict.length - 1) + 4, zoomMin, zoomMax, chartWidth) - offsetX}, ${$toYPixel(data[data.length - 1].y, globalMin, globalMax, chartHeight)})`">
              {{i}}
            </text>
          </g>
          <!-- marcas de anomalias -->
          <line 
          v-for="(line, i) in anomalies"
          :key="'mark' + i"
          :x1="line - offsetX" y1="0" :x2="line - offsetX" :y2="chartHeight"
          stroke-linejoin="round"
          stroke-linecap="round"
          stroke-width="1.5"
          stroke="red"/>
          <!-- ejes -->
          <x-axis
          :transform="`translate(${-offsetX} ${chartHeight})`"
          :range="[zoomMin, zoomMax]"
          :dataset="total"
          x="x"
          :ticks="25"
          :fixed="1"
          :height="chartHeight"
          :width="chartWidth"
          label="minutos"></x-axis>
        </g>
        <y-axis :range="[globalMin, globalMax]" :ticks="5" :fixed="1" :height="chartHeight" label="Units"></y-axis>
      </g>
    </svg>
  </v-flex>
</template>

<script>
export default {
  props: {
    dataSet: null,
    height: {
      type: Number,
      default: 500
    },
    marginLeft: {
      type: Number,
      default: 40
    },
    marginTop: {
      type: Number,
      default: 40
    },
    marginRight: {
      type: Number,
      default: 20
    },
    marginBottom: {
      type: Number,
      default: 30
    },
    padding: {
      type: Number,
      default: 12
    }
  },
  data: () => ({
    width: null,
    chartWidth: 1000,
    chartHeight: 400,
    total: [],
    toPredict: [],
    prediction: [],
    timeSeries: [],
    debug: {},
    globalMax: -10e10,
    globalMin: 10e10,
    zoomMin: 0,
    zoomMax: 100,
    panEnabled: false,
    offsetX: 0,
    isActive: {
      toPredict: true,
      prediction: true
    }
  }),
  mounted () {
    this.width = this.$el.clientWidth
    this.chartWidth = this.width - (this.marginLeft + this.marginRight)
    this.chartHeight = this.height - (this.marginTop + this.marginBottom)
  },
  methods: {
    processData (d) {
      let yValues = []
      for (let i = 0; i < d.toPredict.length; i++) {
        this.toPredict.push({
          x: i,
          y: d.toPredict[i]
        })
        this.total.push({
          x: i,
          y: d.toPredict[i]
        })
        yValues.push({v: d.toPredict[i]})
      }
      this.toPredict.push({
        x: d.toPredict.length,
        y: d.prediction.future[0].value
      })
      for (let i = 0; i < d.prediction.future.length; i++) {
        this.prediction.push({
          x: d.prediction.future[i].step,
          y: d.prediction.future[i].value
        })
        this.total.push({
          x: d.prediction.future[i].step,
          y: d.prediction.future[i].value
        })
        yValues.push({v: d.prediction.future[i].value})
      }
      for (let t = 0; t < d.timeSeries.length; t++) {
        this.$set(this.timeSeries, t, [])
        for (let i = 0; i < d.timeSeries[t].data.length; i++) {
          this.timeSeries[t].push({
            x: i,
            y: d.timeSeries[t].data[i]
          })
          yValues.push({v: d.timeSeries[t].data[i]})
        }
        this.$set(this.isActive, t, true)
      }
      if (d.debug && d.debug.length > 0) {
        d.debug.map(v => {
          this.debug[v.name] = []
          v.data.map(s => {
            this.debug[v.name].push({x: s.step, y: s.value})
            yValues.push({v: s.value})
          })
        })
      }
      this.globalMax = this.$getMax(yValues, 'v')
      this.globalMin = this.$getMin(yValues, 'v')
    },
    zoom (e) {
      if (this.zoomMin && this.zoomMax >= 3000) {
        this.zoomMin = 0
        this.zoomMax = this.total.length - 1
      } else {
        this.zoomMin += e.wheelDelta * 0.02
        this.zoomMax += -e.wheelDelta * 0.02
      }
    },
    pan (e) {
      if (this.panEnabled) {
        this.offsetX += -e.movementX
      }
    },
    reset () {
      this.total = []
      this.toPredict = []
      this.prediction = []
      this.globalMax = -10e10
      this.globalMin = 10e10
    }
  },
  watch: {
    dataSet: {
      handler: function (val) {
        if (val.toPredict && val.prediction) {
          if (!val.debug) {
            this.debug.isEmpty = true
          }
          this.$nextTick(() => {
            this.reset()
            this.processData(val)
          })
        }
      },
      immediate: true
    }
  },
  computed: {
    anomalies () {
      const d = this.dataSet
      if (d.toPredict) {
        let anomalies = []
        for (let i = 0; i < d.prediction.past.length; i++) {
          let xMark = this.$toXPixel(d.prediction.past[i].step, this.zoomMin, this.zoomMax, this.chartWidth)
          anomalies.push(xMark)
        }
        return anomalies
      }
    }
  }
}
</script>

<style>
  svg {
    user-select: none;
  }
</style>



