<template>
  <v-container grid-list-md fluid>
    <v-layout wrap>
      <v-flex xs8>
        <v-card flat class="transparent">
          <v-text-field label="url" v-model="url" outline></v-text-field>
          <v-textarea
            :rules="[rules.isValidJSON]"
            v-model="dataSet"
            label="Data to process"
            outline
            @keypress.enter="getUrl"
          ></v-textarea>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn @click="getUrl" color="green" flat>
              submit
              <v-icon right>send</v-icon>
            </v-btn>
          </v-card-actions>
        </v-card>
        <line-chart :dataSet="toGraph" :height="350" :marginLeft="70"></line-chart>
        <!-- <line-chart :dataSet="toGraph" :real="toGraphReal" :height="350" :marginLeft="70"></line-chart> -->
      </v-flex>
      <v-flex xs4>
        <pre>{{response}}</pre>
      </v-flex>
    </v-layout>
    <v-dialog
      v-model="loading"
      hide-overlay
      persistent
      width="300"
    >
      <v-card>
        <v-card-text>
          Processing... this may take a while
          <v-progress-linear
            indeterminate
            class="mb-0"
          ></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="errorDialog.value"
      hide-overlay
      persistent
      width="500"
    >
      <v-card color="red" dark>
        <v-card-text>
          <pre>{{errorDialog.text}}</pre>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn flat @click="errorDialog.value = false">ok</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
// import prices from '@/assets/dataTest/23-02-2018-h14'
import resultTest from '@/assets/dataTest/univariateResult'
import lineChart from '@/components/timeLineChart'
export default {
  components: {
    lineChart
  },
  data: () => ({
    url: 'http://52.59.213.240:9999/univariate/get',
    dataSet: '{"data":[15136, 16733, 20016, 17708, 18019, 19227, 22893, 23739, 21133, 22591, 26786, 29740,15028, 17977, 20008, 21354, 19498, 22125, 25817, 28779, 20960, 22254, 27392, 29945,16933, 17892,20533, 23569, 22417, 22084, 26580, 27454, 24081, 23451, 28991, 31386, 16896, 20045, 23471, 21747, 25621, 23859, 25500, 30998, 24475, 23145, 29701, 34365, 17556, 22077, 5702,22214,26886, 23191, 27831, 35406, 23195, 25110, 30009, 36242, 18450, 21845, 26488, 22394, 28057, 25451, 24872, 33424, 24052, 28449, 33533, 37351, 19969, 21701, 26249, 24493, 24603,26485, 30723, 34569, 26689, 26157, 32064, 38870, 21337, 19419, 23166, 28286, 24570, 24001, 33151, 24878, 26804, 28967, 33311, 40226, 20504, 23060, 23562, 27562, 23940, 24584,34303, 25517, 23494, 29095, 32903, 34379, 16991, 21109, 23740, 25552, 21752, 20294, 29009, 25500, 24166, 26960, 31222, 38641, 14672, 17543, 25453, 32683, 22449, 22316]}',
    // url: '',
    // dataSet: '',
    response: '',
    toGraph: {},
    toGraphReal: [],
    loading: false,
    errorDialog: {
      value: false,
      text: ''
    },
    rules: {
      isValidJSON: v => {
        try {
          JSON.parse(v)
        } catch (e) {
          return 'Data is not a valid json'
        }
        return true
      }
    }
  }),
  // mounted () {
  //   let pricesData = {data: []}
  //   for (let i = 25; i < 50; i++) {
  //     pricesData.data.push(prices[i].price)
  //   }
  //   this.dataSet = JSON.stringify(pricesData)
  //   for (let i = 25; i < 55; i++) {
  //     this.toGraphReal.push({y: prices[i].price})
  //   }
  // },
  methods: {
    getUrl () {
      this.loading = true
      const parseData = JSON.parse(this.dataSet)
      this.$http.post(this.url, parseData).then(response => {
        this.response = JSON.parse(response.bodyText)
        this.toGraph = {
          toPredict: parseData.data,
          prediction: this.response
        }
        this.loading = false
      }).catch(err => {
        this.loading = false
        this.errorDialog.value = true
        this.errorDialog.text = err
        console.log(err)
      })
    },
    simulateResult () {
      this.response = resultTest
      const parseData = JSON.parse(this.dataSet)
      this.toGraph = {
        toPredict: parseData.data,
        prediction: this.response
      }
    },
    validateJSON (json) {
      try {
        JSON.parse(json)
      } catch (e) {
        return false
      }
      return true
    }
  },
  watch: {
    dataSet: {
      handler: function (val) {
        if (this.validateJSON(val)) {
          this.simulateResult()
        }
      },
      immediate: true
    }
  }
}
</script>



